import React, { useState, useEffect } from 'react';

const Sidebar = ({ sections }) => {
  const [openSections, setOpenSections] = useState({});
  const [animationStates, setAnimationStates] = useState({});
  const [currentPath, setCurrentPath] = useState(window.location.pathname);

  useEffect(() => {
    const handleLocationChange = () => {
      setCurrentPath(window.location.pathname);
    };

    window.addEventListener('popstate', handleLocationChange);
    return () => window.removeEventListener('popstate', handleLocationChange);
  }, []);

  const toggleSection = (section) => {
    if (openSections[section]) {
      setAnimationStates((prev) => ({ ...prev, [section]: 'fade-out' }));
      setTimeout(() => {
        setOpenSections((prev) => ({ ...prev, [section]: false }));
        setAnimationStates((prev) => ({ ...prev, [section]: '' }));
      }, 300);
    } else {
      setOpenSections((prev) => ({ ...prev, [section]: true }));
      setAnimationStates((prev) => ({ ...prev, [section]: 'fade-in' }));
    }
  };

  return (
    <div className="sidebar">
      {sections.map((section, index) => {
        const isSectionActive = currentPath.startsWith(`/${section.id}`);
        return (
          <div key={index} className="sidebar-section">
            <div 
              className="section-header" 
              onClick={() => toggleSection(section.title)}
            >
              <span style={{ color: isSectionActive ? '#446EE4' : 'white' }}>
                {section.title}
              </span>
              <span 
                className="toggle-icon" 
                style={{ transform: openSections[section.title] ? 'rotate(90deg)' : 'rotate(0deg)' }}
              >
                â–º
              </span>
            </div>

            {openSections[section.title] && (
              <div className={`sub-sections ${animationStates[section.title]}`}>
                {section.links.map((link, linkIndex) => {
                  const isLinkActive = currentPath === link.href;
                  return (
                    <a 
                      key={linkIndex} 
                      href={link.href} 
                      className="sub-section-link"
                      style={{ 
                        animationDelay: `${linkIndex * 0.1}s`,
                        color: isLinkActive ? '#446EE4' : '#c2c7d6',
                      }}
                    >
                      {link.text}
                    </a>
                  );
                })}
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
};

export default Sidebar;